export default {
	data() {
		return {
			cover: ''
		}
	},
	methods: {
		operator(){
			uni.showActionSheet({
				itemList: ["保存图片到本地"],
				success: (res)=>{
					// 下标为0则下载
					if (res.tapIndex == 0){
						uni.showLoading({
							title: '图片保存中...'
						})
						uni.downloadFile({
							url: this.cover,
							success: (res) => {
								let tempFilePath = res.tempFilePath
								uni.saveImageToPhotosAlbum({
									filePath: tempFilePath,
									success: (res) => {
										uni.showToast({
											title: "保存成功",
											duration: 2000
										})
									},
									complete: () => {
										uni.hideLoading()
									}
								})
							}
						});
					}
				}
			})
		}
	},
	onLoad(params) {
		const {cover} = params
		this.cover = cover
		// 通过api修改导航栏的属性
		uni.setNavigationBarColor({
			frontColor: "#ffffff",
			backgroundColor: '#000000'
		});
	}
}
